import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import model from './model.json';

const { copy, page, payPal } = window.__MODEL__ ? window.__MODEL__ : model;

ReactDOM.render(
  <App copy={copy} page={page} payPal={payPal} />,
  document.getElementById('root')
);



