import classNames from 'classnames';
import React from 'react';

const Notification = ({ copy, notification, onClick }) => {
  const isSuccess = notification.info ? true : false;
  const isDanger = notification.error ? true : false;
  return (
    <div
      className={classNames({
        notification: true,
        'is-success': isSuccess,
        'is-danger': isDanger
      })}
    >
      <button className="delete" onClick={onClick} />
      {isSuccess &&
        copy[`thanks-for-filling-out-${notification.data.key}-form`]}
      {isDanger && copy['oh-snap']}
    </div>
  );
};

export default Notification;



