/* global paypal */

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Form, Field } from 'react-final-form';
import Notification from './Notification';
import renderControlInput from './renderControlInput';

class PaymentForm extends Component {
  onAuthorize = (data, actions, form) => {
    return actions.payment.execute().then(() => {
      form.submit();
      form.reset();
    });
  };

  payment = (data, actions, values, form) => {
    const { payPal } = this.props;
    return actions.payment.create({
      payment: {
        transactions: [
          { amount: { total: values['amount-paid'], currency: 'USD' } }
        ]
      },
      experience: {
        presentation: {
          brand_name: payPal.brandName,
          logo_image: payPal.logoImage
        }
      }
    });
  };

  validate = values => {
    const { copy } = this.props;
    const errors = {};
    if (!values['amount-paid']) {
      errors['amount-paid'] = copy['required'];
    }
    if (!values['client-name']) {
      errors['client-name'] = copy['required'];
    }
    return errors;
  };

  render() {
    const {
      copy,
      notification,
      onClick,
      onSubmit,
      payPal: { env, liveClientId, sandboxClientId }
    } = this.props;
    const PayPalButton = paypal.Button.driver('react', { React, ReactDOM });

    const client = {
      production: liveClientId,
      sandbox: sandboxClientId
    };

    const style = {
      color: 'silver',
      layout: 'vertical',
      shape: 'rect',
      size: 'responsive',
      tagline: false
    };

    return [
      notification && (
        <Notification
          copy={copy}
          key="notification"
          notification={notification}
          onClick={onClick}
        />
      ),
      <Form
        initialValues={{
          'amount-paid': '',
          'client-name': '',
          key: 'payment'
        }}
        key="form"
        onSubmit={onSubmit}
        render={({ form, handleSubmit, submitting, values }) => (
          <form>
            <div className="field">
              <Field name="client-name">
                {({ input, meta }) =>
                  renderControlInput(
                    input,
                    copy['client-name'],
                    meta,
                    copy['client-name-placeholder'],
                    submitting
                  )
                }
              </Field>
            </div>
            <div className="field">
              <Field name="amount-paid">
                {({ input, meta }) =>
                  renderControlInput(
                    input,
                    copy['amount-paid'],
                    meta,
                    copy['amount-paid-placeholder'],
                    submitting,
                    'number'
                  )
                }
              </Field>
            </div>
            <div className="field">
              <div className="control">
                <PayPalButton
                  client={client}
                  commit={true}
                  env={env}
                  onAuthorize={(data, actions) =>
                    this.onAuthorize(data, actions, form)
                  }
                  onCancel={() => console.log('onCancel')}
                  onError={() => {
                    console.log('onError');
                    handleSubmit();
                  }}
                  payment={(data, actions) =>
                    this.payment(data, actions, values, form)
                  }
                  style={style}
                />
              </div>
            </div>
          </form>
        )}
        validate={this.validate}
      />
    ];
  }
}

export default PaymentForm;



